import React, { useContext, useEffect, useState } from "react";
import styled from "styled-components";
import { Menu, MenuAlt1 } from "@styled-icons/heroicons-solid";
import { ThemeContext } from "../context/Theme";
import gsap from "gsap";

export const Nav = () => {
  const [open, setOpen] = useState(false);
  const theme = useContext(ThemeContext);

  const icon = open ? (
    <MenuAlt1 size="50" id="open-menu" onClick={() => setOpen(false)} />
  ) : (
    <Menu size="50" id="close-menu" onClick={() => setOpen(true)} />
  );

  useEffect(() => {}, [open]);

  return (
    <Container id="nav" theme={theme}>
      {icon}
    </Container>
  );
};

const Container = styled.nav`
  top: 0;
  left: 0;
  position: fixed;

  z-index: 10;
  display: flex;
  border-radius: 0px 0px 5px 0px;

  transition: opacity 5s;

  #hamburger:hover {
    cursor: pointer;
    color: ${(props) => props.theme.main};
  }

  @media screen and (max-width: 64em) {
    background-color: ${(props) => props.theme.bg_nav};
  }
  @media screen and (min-width: 64em) {
    background-color: transparent;
  }
`;
